<template>
    <div style="display: flex; justify-content: space-between; ">
        <u-select width="200px" height="32px" v-model="model10" multiple>
            <u-option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</u-option>
        </u-select>
        <u-select v-model="model3" width="200px" height="32px" :skin="img13" rule="1,3||20,10,20,10||2" :drop-skin="img14" drop-rule="||20,10,20,10">
            <u-option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</u-option>
        </u-select>
        <u-select size="large" v-model="model4" width="200px" height="32px">
            <u-option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</u-option>
        </u-select>
        <u-select skin="none" drop-skin="none" color="red" border="1px solid #4b5568" background-color="#3c4454|#4b5568" drop-color="#8d97ac" drop-background-color="#3c4454|#4b5568" size="large" v-model="model4" width="100px" height="25px">
            <u-option v-for="item in cityList" color="#8d97ac" background-color="|red" padding="4px 16px" :value="item.value" :key="item.value">{{ item.label }}</u-option>
        </u-select>

        <u-select skin="none" drop-skin="none" color="#8d97ac" border="1px solid #4b5568" background-color="#252a37|#252a37" drop-color="#8d97ac"
            drop-background-color="#252a37|#252a37" width="61px" height="24px" drop-height="80px" drop-width="61px" padding="2px 17px 2px 5px">
            <u-option v-for="item in cityList" color="#8d97ac|#f9a82e" background-color="|#252a37" padding="2px 17px 2px 5px" :value="item.value" :key="item.value">{{ item.label }}</u-option>
        </u-select>
        <u-select skin="none" drop-skin="none" text-align="center" drop-text-align="center" color="#8d97ac" border="1px solid #4b5568" background-color="#252a37|#252a37" drop-color="#8d97ac"
            drop-background-color="#252a37|#252a37" width="200px" height="36px" padding="2px 17px 2px 5px">
            <u-option v-for="item in cityList" color="#8d97ac|#f9a82e" background-color="|#252a37" :value="item.value" :key="item.value">{{ item.label }}</u-option>
        </u-select>
    </div>
</template>
<script>
    import img13 from './img/usercard/btn_enter.png';
    import img14 from './img/usercard/head_bg_normal.png';
    export default {
        data () {
            return {
                cityList: [
                    {
                        value: 'New York',
                        label: 'New York'
                    },
                    {
                        value: 'London',
                        label: 'LondonLondonLondonLondonLondonLondonLondonLondonLondonLondonLondonLondonLondonLondonLondonLondonLondonLondon'
                    },
                    {
                        value: 'Sydney',
                        label: 'Sydney'
                    },
                    {
                        value: 'Ottawa',
                        label: 'Ottawa'
                    },
                    {
                        value: 'Paris',
                        label: 'Paris'
                    },
                    {
                        value: 'Canberra',
                        label: 'Canberra'
                    }
                ],
                model3: 'Paris',
                model4: '',
                img13: img13,
                img14: img14,
                model10: [],
            };
        }
    };
</script>



